.phonesized-devlopedby-L,
.phonesized-devlopedby-S {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 30%;
    color: white;
    font-weight: bold;
}

.phonesized-devlopedby-S {
    display: none;
}

.phonesized-devlopedby-L p,
.phonesized-devlopedby-S p {
    text-align: center;
}

.phonesized-devlopedby-L span,
.phonesized-devlopedby-S span {
    color: rgb(255, 179, 0);
    padding-right: 2px;
    text-wrap: balance;
    text-align: center;
}

@media screen and (max-width:480px) {

    .phonesized-devlopedby-L {
        display: none;
    }

    .phonesized-devlopedby-S {
        display: flex;
        height: 50%;
        width: 50%;
    }

    .social-media {
        height: 50%;
        width: 45%;
        justify-content: space-evenly;
    }

    .social-media p {
        margin-top: 5px;
        font-size: 1.2em;

    }

    .social-media a {
        font-size: 0.8em;
    }

    .social-media-links {
        grid-template-rows: repeat(2, 1fr);
        grid-template-columns: repeat(3, 1fr);
    }


    .rights {
        height: 50%;
        width: 100%;
        border: none;
        font-size: calc(1.5vw + 7px);
    }
}